.wrapper {
  padding-top: 30px;
}
.search-form {
  margin-bottom: 15px;
  border-bottom: 1px solid #dcdfe6;
}

.title:not(:first-child) {
  margin-top: 30px;
}

.info {
  padding-inline-start: 0;
}
.info > li {
  padding: 5px 0;
}

.params-options {
  display: flex;
  justify-content: space-between;
}
.params-options:not(:last-child) {
  margin-bottom: 10px;
}
.params-list {
  padding: 5px 15px;
  background: white;
}
.params-list__item {
  display: flex;
  justify-content: space-between;
}
.params-list__item:first-child {
  color: #c0c4cc;
}
.params-list__item > * {
  flex: 1;
}
.params-list__item > *:first-child {
  flex: 2;
}
.params-list__item > *:not(:first-child) {
  text-align: right;
}

.total {
  border-top: 1px solid #dcdfe6;
}
.total__item {
  display: flex;
  justify-content: space-between;
}
.total__item--discount {
  color: #DC2A2A;
}
.total__item:not(.total__item--discount) > *:first-child {
  color: #c0c4cc;
}

.payment-methods :global .el-radio {
  margin: 5px !important;
}
.payment-methods :global .vacc-disabled {
  margin-bottom: 20px;
}
.payment-methods :global .vacc-disabled::after {
  content: "(服務前一日不提供)";
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  color: #c2c6ce;
}